@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&disply=swap');
@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    border: none;
    outline: none;
    scroll-behavior: smooth;
    font-family: 'Poppins', sans-serif;
}

:root {
    --bg-color: #081b29;
    --second-bg-color: #112e42;
    --text-color: #ededed;
    --main-color: #00abf0;
}

body {
    background: var(--second-bg-color);
    color: var(--text-color);
}

.outer-box {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translateX(-50%), translateY(-50%);
}

.inner-box {
    width: 30%;
    border: 2px solid var(--main-color);
    padding: 50px;
    text-align: center;
    background: var(--bg-color);
    border-radius: 10px;
}

.inner-box h2 {
    font-size: 25px;
    margin-bottom: 20px;
}

.inner-box h2 span {
    color: var(--main-color);
    font-size: 25px;
    margin-bottom: 30px;
}

.inner-box form input {
    display: inline-block;
    width: 90%;
    padding: 10px 10px 10px 0;
    margin-bottom: 30px !important;
    background: var(--bg-color);
    border-bottom: 2px solid var(--main-color);
    color: var(--text-color);
    font-size: 18px;
}
.inner-box form input::-webkit-inner-spin-button,
.inner-box form input::-webkit-outer-spin-button {
    -webkit-appearance: none;
}

.inner-box form button {
    display: inline-block;
    padding: 10px;
    width: 90%;
    background: var(--main-color);
    border-radius: 8px;
    box-shadow: 0 0 1rem var(--main-color);
    font-size: 20px;
    color: var(--second-bg-color);
    transition: .5s ease;
}

.inner-box form button:hover {
    box-shadow: none;
}